header {
  margin: 0 auto;
}

.header-search {
  margin-left: 25px;
}
.main-menu {
  width: 1120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: rgb(99, 98, 98);
  a {
    text-decoration: none;
    color: white;
    &:visited {
      color: white;
    }
    &:hover {
      color: #f0e4d1;
    }
  }
  li {
    display: inline;
    font-family: Novecento Sans Web, sans-serif;
    text-transform: uppercase;
    text-shadow: 1px 1px 2px #323232;
    padding-left: 1.5rem;
  }

  img {
    width: 50px;
    margin-left: 0;
  }
  padding: 0 130px 0 130px;
}

.main-menu2 {
  background-color: hsla(0, 0%, 100%, 0.85);
  width: 1120px;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e1e1e1;

  a {
    text-decoration: none;
    color: #248a83;
    &:visited {
      color: $green;
    }
    &:hover {
      color: #2f1339;
    }
  }
  li {
    display: inline;
    font-family: Novecento Sans Web, sans-serif;
    text-transform: uppercase;
    padding-left: 1.5rem;
  }

  img {
    width: 50px;
    margin-left: 0;
  }
  .header-search {
    input {
      color: $darkGrey;
    }

    fieldset {
      color: $darkGrey;
    }
  }
  padding: 0 130px 10px 130px;
}

.nav-logo {
  background-image: url('/red/projects/project-04/wp-content/themes/inhabitant/images/logos/inhabitent-logo-tent-white.svg');
  background-size: 50px 33px;
  background-repeat: no-repeat;

  h1 {
    height: 33px;
    margin: 0;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    width: 50px;
  }
}

.nav-logo2 {
  background-image: url('/red/projects/project-04/wp-content/themes/inhabitant/images/logos/inhabitent-logo-tent.svg');
  background-size: 50px 33px;
  background-repeat: no-repeat;
  h1 {
    height: 33px;
    margin: 0;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    width: 50px;
  }
}

.nav-logo3 {
  display: none;
  background-image: url('/red/projects/project-04/wp-content/themes/inhabitant/images/logos/inhabitent-logo-tent.svg');
  background-size: 50px 33px;
  background-repeat: no-repeat;
  h1 {
    height: 33px;
    margin: 0;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    width: 50px;
  }
}

.search-field {
  display: none;
}

.header-search {
  input {
    background-color: transparent;
    border: none;
    color: white;
    font-family: Novecento Sans Web, sans-serif;
    font-size: 1em;
  }
}
